	/* --- Google Fonts --- */

@import url('https://fonts.googleapis.com/css2?family=Open+Sans&family=Recursive&display=swap');

* {
    font-family: 'Open Sans', sans-serif;
}

  /* ------- Navbar ------- */
.navbar {
    background-color: #D5BA83;
}

.navbar a:hover {
    color: #52575D;
    transition: 0.5s;
}

.navbar a img {
    width: 40px;
    margin-right: 10px;
}

#navbarMenu {
    text-align: center;    
}  

#navbarMenu a {
    color: #52575D;  
}

#navbarMenu a:hover {
    color: #929eaa;
}

    /* -------- Home --------- */
.home {
    background-image: url(../Images/homeimg.png);
    background-position: bottom;
    background-size: cover;
    height: 89vh;
    
}
.home-2 {
    padding: 150px 0px;
}
.home a {
    color: #FFFF;
    width: 220px;
    height: 62px;
    background: #52575D;
    border-radius: 50px;
}
.home a:hover {
	color: #FFFF;
	background-color: #929eaa;
	transition: 0.6s;
}
@media (max-width: 989px) { 
    .home img {
        display: none;
    }
}
    /*------ Form -------- */
.formstyle {
    border: royalblue solid ;
    border-radius: 25px;
    background-color: #FDF364;
    box-shadow: 10px 10px #D5BA83 ;
}
/* ------- Contenido --------- */
.content {
    background-image: url(../Images/imagecontent.png);
    height: 500px; 
    border-radius: 25px;
}
.name-fun {
    margin-top: 220px;
    transform: rotate(-90deg);  
}
        /*----- Footer ------- */
.footer {
    background: #fdf364;
    color: #52575D;
}
.footer h4 {
    font-weight: bold;
}

.footer a {
    color: #52575D;
    text-decoration: none;
    font-size: 30px;
}
.footer a:hover {
    color: #929eaa;
    opacity: 6;
    text-decoration: none;
    transition: 0.6s;
}
.footer-links a {
    padding: 0px 10px;
}
.footer-copyright {
    color: #ffff;
    background-color: #463921;
    padding: 0;
}
.footer-copyright a {
    margin-left: 5px;
    color: #ffff;
    font-size: 15px;
}
@media (max-width: 768px) { 
    .footer-2{
        flex-direction: column;
    }
 }
 /* ----- Contact ------ */
.contact {
     background-image: url(../Images/imagecontact.png);
     background-size: cover;
     background-position: center;
     border-radius: 25px;
     height: 150px; 
}
.contact-2 {
    margin: 150px 0px;
}